<template>
    <el-card class="login-container">
        <el-form ref="form" label-width="70px" class="login-contain" :inline="true" :model="form" :rules="rules">
            <h3 class="login-title">系统登陆</h3>
            <el-form-item label="用户名" prop="username">
                <el-input placeholder="请输入账号" v-model="form.username">
                </el-input>
            </el-form-item>
            <el-form-item label="密码" prop="password">
                <el-input @keyup.enter.native="submit" type="password" placeholder="请输入密码" v-model="form.password">
                </el-input>
            </el-form-item>
            <el-form-item>
                <el-button @click="submit" type="primary">登录</el-button>
            </el-form-item>
        </el-form>
    </el-card>
</template>

<script>
import Mock from 'mockjs'
import Cookie from 'js-cookie'
import { getMenu } from '../api'
export default {
    data() {
        return {
            form: {
                username: '',
                password: ''
            },
            rules: {
                username: [
                    { required: true, trigger: 'blur', message: '请输入用户名' }
                ],
                password: [
                    { required: true, trigger: 'blur', message: '请输入密码' }
                ]
            }
        }
    },
    methods: {
        submit() {
            this.$refs.form.validate((valid) => {
                if (valid) {
                    getMenu(this.form).then(({ data }) => {
                        console.log(data)
                        if (data.code === 20000) {
                            Cookie.set('token', data.data.token)
                            this.$router.push('/home')
                        } else {
                            this.$message.error(data.data.message);
                        }
                    })
                }
            })
            // this.$router.push('/home')
        }
    }
}
</script>

<style lang="less" scoped>
.login-container {
    width: 350px;
    margin: 180px auto;
    border-radius: 25px;

    .login-contain {
        display: flex;
        flex-flow: column;
        align-items: center;

        .login-title {
            font-size: 2rem;
            text-align: center;
            margin: 20px 0;
            color: #505458;
        }

        .el-input {
            width: 198px;
        }
    }
}
</style>